export const fetchUpdate = async ( urlApi,dataApi,id ) => {
  try {
    const response = await fetch(urlApi + "/" + id, {
      method: "PUT",
      body: dataApi,
      headers: { "Content-type": "application/json" }
    });
    response.json();
    if (200 <= response.status && response.status <= 299) {
      console.log('PUT ' + response.status + ' Actualizaci贸n exitosa');
    } else if (400 <= response.status && response.status <= 499) {
      console.log('PUT ' + response.status + ' Actualizaci贸n fallida: Error en el env铆o de datos');
    } else if (500 <= response.status && response.status <= 599) {
      console.log('PUT ' + response.status + ' Actualizaci贸n fallida: Error en el servidor remoto');
    }
    return response.status;
  } catch (error) {
      const errorMessage = error.toString().split(':')[1].trim();
      if (errorMessage === 'Failed to fetch') { console.log('Actualizaci贸n fallida: No hay conexi贸n con la base de datos') } 
      else { console.log('Actualizaci贸n fallida: ' + errorMessage) }
  }
}